.shadow {
    position: absolute;
    width: 100%;
    height: 80vh;
    background-color: rgba(0, 0, 255, 0.226);

}

.white-block {
    position: absolute;
    height: 55px;
    width: 60%;
    background-color: #fff;

    &-left {
        bottom: 19%;
        left: -50px;
        transform: skew(45deg);
    }
}

.header {
    background-image: url(./img/bora_640.jpg);
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 80vh;
    padding: 2em;
    background-attachment: scroll;
    text-align: center;
    color: #fff;

    &__title {
        font-size: 3.5rem;
        font-weight: 300px;
        z-index: 1;
    }

    &__text {
        font-size: 1.4rem;
        z-index: 1;
    }
}

@media (min-width: 768px) {
    .white-block {
        &-left {
            bottom: 19%;
            left: -200px;
            transform: skew(45deg);
        }
    }

    .header {
        background-image: url(./img/bora_1920.jpg);

        &__title {
            font-size: 5rem;

        }

        &__text {
            font-size: 2rem;

        }
    }
}